<template>
  <div>
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.push('myjp')" />

    <van-tabs v-model="active">
      <van-tab title="全部订单">
        <div v-for="(item,index) in allOrders" :key="index">
          <van-panel :title="item.name" :status="status(item.type)">
            <div class="shopping">
              <van-card
                :num="item.num"
                :price="item.price"
                desc="描述信息"
                :title="item.name"
                :thumb="item.img"
              />
            </div>
            <div slot="footer">
              <van-button size="small" @click="del(index)">{{state(item.type)}}</van-button>
              <van-button
                size="small"
                type="danger"
                @click="payment(index)"
              >{{condition(item.type)}}</van-button>
            </div>
          </van-panel>
        </div>
      </van-tab>

      <van-tab title="待付款">
        <div v-for="(item,index) in allOrders" :key="index">
          <van-panel :title="item.name" :status="status(item.type)" v-if="item.type==1">
            <div class="shopping">
              <van-card
                :num="item.num"
                :price="item.price"
                desc="描述信息"
                :title="item.name"
                :thumb="item.img"
              />
            </div>
            <div slot="footer">
              <van-button size="small" @click="del(index)">{{state(item.type)}}</van-button>
              <van-button size="small" type="danger" @click="payment(index)">{{condition(item.type)}}</van-button>
            </div>
          </van-panel>
        </div>
      </van-tab>

      <van-tab title="待发货">
        <div v-for="(item,index) in allOrders" :key="index">
          <van-panel :title="item.name" :status="status(item.type)" v-if="item.type==2">
            <div class="shopping">
              <van-card
                :num="item.num"
                :price="item.price"
                desc="描述信息"
                :title="item.name"
                :thumb="item.img"
              />
            </div>
            <div slot="footer">
              <van-button size="small" @click="del(index)">{{state(item.type)}}</van-button>
              <van-button size="small" type="danger" @click="payment(index)">{{condition(item.type)}}</van-button>
            </div>
          </van-panel>
        </div>
      </van-tab>

      <van-tab title="待收货">
        <div v-for="(item,index) in allOrders" :key="index">
          <van-panel :title="item.name" :status="status(item.type)" v-if="item.type==3">
            <div class="shopping">
              <van-card
                :num="item.num"
                :price="item.price"
                desc="描述信息"
                :title="item.name"
                :thumb="item.img"
              />
            </div>
            <div slot="footer">
              <van-button size="small" @click="del(index)">{{state(item.type)}}</van-button>
              <van-button size="small" type="danger" @click="payment(index)">{{condition(item.type)}}</van-button>
            </div>
          </van-panel>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data() {
    return {
      active: 0,
      allOrders: [], //全部订单
      obligationList: [], //待付款
      paid: [], //待发货
      receivedGoods: [] //已收货
    };
  },
  methods: {
    status(type) {
      if (type == 1) {
        return "待付款";
      } else if (type == 2) {
        return "待发货";
      } else if (type == 3) {
        return "已发货";
      }
    },
    state(type) {
      if (type == 1) {
        return "取消订单";
      } else if (type == 2) {
        return "取消订单";
      } else if (type == 3) {
        return "查看物流";
      }
    },
    condition(type) {
      if (type == 1) {
        return "立即支付";
      } else if (type == 2) {
        return "提醒发货";
      } else if (type == 3) {
        return "确认收货";
      }
    },
    payment(index) {
      if (this.allOrders[index].type == 1) {
        this.$store.state.myjp.allOrders[index].type = 2;
        this.allOrders[index].type = 2;
        this.allOrders = this.$store.state.myjp.allOrders.filter(item=>{return item})
      }else if(this.allOrders[index].type == 2){
        Toast('已提醒卖家发货');
      }
    },
    del(index){
       this.$store.state.myjp.allOrders.splice(index,1)
       this.allOrders = this.$store.state.myjp.allOrders.filter(item=>{return item})
    }
  },
  created() {
    // console.log(this.$store.state.myjp.allOrders);
    this.$store.state.bool = false
    this.allOrders = this.$store.state.myjp.allOrders;
    this.active = this.$route.query.num
  }
};
</script>

<style lang="scss" scoped>
.van-panel__footer {
  text-align: right;
}
</style>